.patient-home-page h1 {
  padding: 0;
  margin: 0;
}

.patient-home-page {
  background-image: url("../../img/10.jpg");
  background-size: 100%;
  height: 97vh;
}

.patient-home-page .aboveTheContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.patient-home-page .container {
  border: 4px solid white;
  background-color: rgba(255, 255, 255, 0.881);
  width: 286px;
  height: 563px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5%;
  gap: 22px;
}

.patient-home-page h1 {
  width: 252px;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 600;
  font-size: 24px;
  line-height: 36px;
  color: rgba(147, 110, 82, 0.88);
}

.patient-home-page .btn-help {
  border: none;
  background-color: rgba(223, 168, 126, 0.711);
  color: white;
  font-size: 24px;
  font-weight: bold;
  cursor: pointer;
  box-sizing: border-box;
  width: 163px;
  height: 163px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 50%;
}

.patient-home-page .btn-FaUserCog {
  border: none;
  border-radius: 50%;
  box-sizing: border-box;
  width: 85px;
  height: 85px;
  background: rgba(44, 197, 244, 0.52);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.patient-home-page .FaUserCog {
  width: 60px;
  height: 45px;
}

.patient-home-page .btn-VscFiles {
  border: none;
  border-radius: 50%;
  box-sizing: border-box;
  width: 85px;
  height: 85px;
  background: rgba(44, 197, 244, 0.52);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.patient-home-page .VscFiles {
  width: 42px;
  height: 47px;
}

.patient-home-page .btn-FaFileMedical {
  border: none;
  border-radius: 50%;
  box-sizing: border-box;
  width: 85px;
  height: 85px;
  background: rgba(44, 197, 244, 0.52);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.patient-home-page .FaFileMedical {
  width: 39px;
  height: 47px;
}

.patient-home-page .btn-FaPhotoVideo {
  border: none;
  border-radius: 50%;
  box-sizing: border-box;
  width: 85px;
  height: 85px;
  background: rgba(44, 197, 244, 0.52);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.patient-home-page .FaPhotoVideo {
  width: 60px;
  height: 45px;
}

.patient-home-page .btnFContainer {
  padding-left: 172px;
}

.patient-home-page .btn-F {
  border: none;
  border-radius: 50%;
  box-sizing: border-box;
  width: 85px;
  height: 85px;
  background: rgba(44, 197, 244, 0.52) url("../../img/6.png") center center
    no-repeat;
  background-size: 70%;
  background-position: 64%;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.patient-home-page .logout {
  cursor: pointer;
  font-family: "Poppins";
  font-style: normal;
  color: #58929c;
  display: flex;
  justify-content: right;
  margin-right: 16px;
  margin-bottom: 57px;
}

@media only screen and (max-width: 1250px) {
  .patient-home-page {
    background-image: url("../../img/12.png");
  }
}

@media only screen and (max-width: 680px) {
  .patient-home-page {
    background-image: url("../../img/11.jpg");
  }
}

@media only screen and (max-width: 350px) {
  .patient-home-page .container {
    width: 266px;
    height: 470px;
    gap: 14px;
  }

  .patient-home-page h1 {
    font-size: 21px;
  }

  .patient-home-page .btn-help {
    width: 145px;
    height: 145px;
    font-size: 20px;
  }

  .patient-home-page .btn-FaUserCog {
    width: 75px;
    height: 75px;
  }

  .patient-home-page .FaUserCog {
    width: 50px;
    height: 35px;
  }

  .patient-home-page .btn-VscFiles {
    width: 75px;
    height: 75px;
  }

  .patient-home-page .VscFiles {
    width: 32px;
    height: 37px;
  }

  .patient-home-page .btn-FaFileMedical {
    width: 75px;
    height: 75px;
  }

  .patient-home-page .FaFileMedical {
    width: 29px;
    height: 37px;
  }

  .patient-home-page .btn-FaPhotoVideo {
    width: 75px;
    height: 75px;
  }

  .patient-home-page .FaPhotoVideo {
    width: 40px;
    height: 35px;
  }

  .patient-home-page .btnFContainer {
    padding-left: 162px;
  }

  .patient-home-page .btn-F {
    width: 75px;
    height: 75px;
  }
}
