.signup {
  background-image: url("../../img/10.jpg");
  background-size: 100%;
  height: 97vh;
}

.signup .aboveTheContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.signup .container {
  border: 4px solid white;
  background-color: rgba(255, 255, 255, 0.881);
  width: 392px;
  height: 437px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5%;
  gap: 3px;
}

.signup .container1 {
  border: 4px solid white;
  background-color: rgba(255, 255, 255, 0.881);
  width: 392px;
  height: 300px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5%;
  gap: 3px;
}

.signup form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
}

.signup h1 {
  color: #005671;
  text-align: center;
  font-family: "Poppins";
  font-style: normal;
  font-size: 26px;
}

.signup .text {
  font-family: "Poppins";
  font-weight: 300;
  font-size: 20px;
}

.signup .textbox {
  box-sizing: border-box;
  width: 153px;
  height: 42px;
  background: rgba(160, 164, 165, 0.29);
  border: 1px solid #000000;
}

.signup .emailTextBox {
  box-sizing: border-box;
  width: 327px;
  height: 42px;
  background: rgba(160, 164, 165, 0.29);
  border: 1px solid #000000;
}

.signup .diploma {
  box-sizing: border-box;
  width: 327px;
  height: 42px;
  background: rgba(160, 164, 165, 0.29);
  border: 1px solid #000000;
}

.signup .textbox::placeholder,
.signup .emailTextBox::placeholder {
  color: black;
}

.custom-file-input::-webkit-file-upload-button {
  visibility: hidden;
}

.custom-file-input::before {
  font-family: "Poppins";
  content: "Select Diplona";
  display: inline-block;
  padding: 10px 2px;
  cursor: pointer;
  font-style: normal;
  font-size: 23px;
  font-weight: 300;
}

.custom-file-input:active::before {
  background: -webkit-linear-gradient(top, #e3e3e3, #f9f9f9);
}

.signup .btn {
  border: none;
  opacity: 67%;
  color: white;
  font-size: 24px;
  font-weight: bold;
  transition: all 0.5s;
  cursor: pointer;
  width: 327px;
  height: 51px;
  background: #7ec8df;
}

.signup .gohome {
  cursor: pointer;
  font-family: "Poppins";
  font-style: normal;
  color: #58929c;
  display: flex;
  justify-content: right;
  margin-right: 16px;
  margin-bottom: 103px;
}

.signup .gohome1 {
  cursor: pointer;
  font-family: "Poppins";
  font-style: normal;
  color: #58929c;
  display: flex;
  justify-content: right;
  margin-right: 16px;
  margin-bottom: 180px;
}

@media only screen and (max-width: 1250px) {
  .signup {
    background-image: url("../../img/12.png");
  }
}

@media only screen and (max-width: 680px) {
  .signup {
    background-image: url("../../img/11.jpg");
  }
}

@media only screen and (max-width: 450px) {
  .signup .container {
    width: 284px;
    height: 363px;
  }

  .signup .container1 {
    width: 286px;
    height: 265px;
  }

  .signup h1 {
    font-size: 23px;
  }

  .signup .text {
    font-size: 16px;
  }

  .signup .textbox {
    width: 129px;
    height: 35px;
  }

  .signup .emailTextBox {
    width: 279px;
    height: 35px;
  }

  .signup .diploma {
    width: 279px;
    height: 35px;
  }

  .custom-file-input::before {
    font-size: 16px;
  }

  .signup .btn {
    width: 279px;
    height: 35px;
    font-size: 20px;
  }
}
