.login-page .aboveTheContainer,
.login-page .aboveTheContainer h1 {
  padding: 0;
  margin: 0;
}

.login-page {
  background-image: url("../../img/10.jpg");
  background-size: 100%;
  height: 97vh;
}

.login-page .aboveTheContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.login-page .container {
  border: 4px solid white;
  background-color: rgba(255, 255, 255, 0.881);
  width: 298px;
  height: 409px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5%;
  gap: 40px;
}

.login-page h1 {
  text-align: center;
  width: 305px;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-size: 32px;
  line-height: 48px;
  color: #936e52;
}

.login-page form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
}

.login-page .email {
  width: 246px;
  height: 55px;
  background: rgba(160, 164, 165, 0.42);
  font-family: "Poppins";
  font-style: normal;
  font-weight: 300;
  font-size: 24px;
  line-height: 36px;
}

.login-page .password {
  width: 246px;
  height: 55px;
  background: rgba(160, 164, 165, 0.42);
  font-family: "Poppins";
  font-style: normal;
  font-weight: 300;
  font-size: 24px;
  line-height: 36px;
  margin: 20px 0px;
}

.login-page .email::placeholder,
.login-page .password::placeholder {
  color: black;
}

.login-page .btn {
  border: none;
  opacity: 67%;
  color: white;
  font-size: 24px;
  font-weight: bold;
  transition: all 0.5s;
  cursor: pointer;
  width: 253px;
  height: 65px;
  background: #7ec8df;
}

.login-page .gohome {
  cursor: pointer;
  font-family: "Poppins";
  font-style: normal;
  color: #58929c;
  display: flex;
  justify-content: right;
  margin-right: 16px;
  margin-bottom: 103px;
}

@media only screen and (max-width: 1250px) {
  .login-page {
    background-image: url("../../img/12.png");
  }
}

@media only screen and (max-width: 680px) {
  .login-page {
    background-image: url("../../img/11.jpg");
  }
}

@media only screen and (max-width: 350px) {
  .login-page .container {
    width: 230px;
    height: 376px;
  }

  .login-page h1 {
    font-size: 26px;
  }

  .login-page .email {
    width: 198px;
    height: 48px;
  }

  .login-page .password {
    width: 198px;
    height: 48px;
  }

  .login-page .btn {
    width: 198px;
    height: 48px;
  }
}
