.editMedicalCard h1 {
  padding: 0;
  margin: 0;
}

.editMedicalCard {
  background-image: url("../../img/10.jpg");
  background-size: 100%;
  height: 97vh;
}

.editMedicalCard .aboveTheContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.editMedicalCard .container {
  border: 4px solid white;
  background-color: rgba(255, 255, 255, 0.881);
  width: 392px;
  height: 539px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5%;
}

.editMedicalCard form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 10px;
}

.editMedicalCard .h1Container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 15px;
}
.editMedicalCard h1 {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 600;
  font-size: 26px;
  line-height: 39px;
  color: #005671;
}

.editMedicalCard a {
  font-family: "Poppins";
  font-size: 18px;
}

.editMedicalCard .inp {
  box-sizing: border-box;
  width: 172px;
  height: 35px;
  background: rgba(160, 164, 165, 0.29);
  border: 1px solid #000000;
}

.editMedicalCard .EmergencyContainer {
  display: flex;
  flex-direction: column;
}

.editMedicalCard .emergencyContactPerson {
  box-sizing: border-box;
  width: 172px;
  height: 35px;
  background: rgba(160, 164, 165, 0.29);
  border: 1px solid #000000;
}

.editMedicalCard .BtnContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 20px;
}

.editMedicalCard .btnRegularMedications {
  border: none;
  opacity: 67%;
  color: white;
  font-size: 19px;
  font-weight: bold;
  transition: all 0.5s;
  cursor: pointer;
  width: 50px;
  height: 30px;
  background: #7ec8df;
}

.editMedicalCard .regularMedicationsText {
  width: 330px;
  height: 44px;
}

.editMedicalCard .btnUpdate {
  border: none;
  opacity: 67%;
  color: white;
  font-size: 24px;
  font-weight: bold;
  transition: all 0.5s;
  cursor: pointer;
  width: 220px;
  height: 35px;
  background: #7ec8df;
}

::placeholder {
  color: white;
}

.editMedicalCard .gohome {
  cursor: pointer;
  font-family: "Poppins";
  font-style: normal;
  color: #58929c;
  display: flex;
  justify-content: right;
  margin-right: 16px;
  margin-bottom: 72px;
}

@media only screen and (max-width: 1250px) {
  .editMedicalCard {
    background-image: url("../../img/12.png");
  }
}

@media only screen and (max-width: 680px) {
  .editMedicalCard {
    background-image: url("../../img/11.jpg");
  }
}

@media only screen and (max-width: 450px) {
  .editMedicalCard .container {
    width: 326px;
    height: 438px;
  }

  .editMedicalCard h1 {
    font-size: 23px;
  }

  .editMedicalCard a {
    font-size: 14px;
  }

  .editMedicalCard .inp {
    width: 100px;
    height: 28px;
  }

  .editMedicalCard .EmergencyContainer {
    flex-direction: initial;
    gap: 0;
  }

  .editMedicalCard .emergencyContactPerson {
    width: 117px;
    height: 28px;
  }

  .editMedicalCard .emergencyContactPerson::placeholder,
  .editMedicalCard .inp::placeholder {
    color: black;
  }

  .editMedicalCard .BtnContainer {
    padding-top: 8px;
  }

  .editMedicalCard .btnRegularMedications {
    width: 50px;
    height: 30px;
  }

  .editMedicalCard .regularMedicationsText {
    width: 309px;
    height: 36px;
  }

  .editMedicalCard .btnUpdate {
    width: 126px;
    height: 31px;
  }
}

@media only screen and (max-width: 370px) {
  .editMedicalCard .container {
    width: 241px;
    height: 454px;
  }

  .editMedicalCard .EmergencyContainer {
    flex-direction: column;
    gap: 10px;
  }

  .editMedicalCard .emergencyContactPerson {
    width: 117px;
    height: 28px;
  }

  .editMedicalCard .btnRegularMedications {
    width: 39px;
    height: 25px;
    font-size: 14px;
  }

  .editMedicalCard .regularMedicationsText {
    width: 225px;
    height: 36px;
  }

  .editMedicalCard .btnUpdate {
    width: 112px;
    height: 29px;
    font-size: 18px;
  }
}
