.home-page h1,
.home-page .par-wrapper,
.home-page .par-wrapper p{
  padding: 0;
  margin: 0;
}

.home-page {
  background-image: url("../../img/10.jpg");
  background-size: 100%;
  height: 97vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 20px;
}

.home-page .container{
  border: 4px solid white;
  background-color: rgba(255, 255, 255, 0.881);
  width: 298px;
  height: 495px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5%;
}

.home-page h1 {
  text-align: center;
  width: 305px;
  font-family: 'Pontano Sans';
  font-style: normal;
  font-weight: 400;
  font-size: 60px;
  line-height: 96px;
  color: #936E52;
}

.home-page .par-wrapper {
  width: 246px;
  font-family: 'Pontano Sans';
  font-style: normal;
  font-weight: 400;
  font-size: 27px;
  line-height: 35px;
  text-align: center;
  color: rgba(156, 118, 88, 0.78);
}

.home-page .btn-help{
  border: none;
  background-color: rgba(223, 168, 126, 0.711);
  color: white;
  font-size: 24px;
  font-weight: bold;
  cursor: pointer;
  box-sizing: border-box;
  width: 240px;
  height: 240px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 50%;
  margin: 20px 0px;
}

.home-page .btn-registration{
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 275;
  font-size: 18px;
  line-height: 27px;
  color: #B54414;
  cursor: pointer;
}

.home-page .noAccount{
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 275;
  font-size: 18px;
  line-height: 27px;
}

.home-page .logIn{
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 275;
  font-size: 23px;
  line-height: 27px;
  cursor: pointer;
}

@media only screen and (max-width:1250px) {
  .home-page {
    background-image: url("../../img/12.png");
  }
}

@media only screen and (max-width:680px) {
  .home-page {
    background-image: url("../../img/11.jpg");
  }
}

@media only screen and (max-width:350px) {

  .home-page .container{
    width: 228px;
    height: 415px;
  }

  .home-page h1 {
    font-size: 40px;
    line-height: 80px;  
  }
  
  .home-page .par-wrapper {
    width: 200px;
    font-size: 18px;
  }
  
  .home-page .btn-help{
    font-size: 20px;
    width: 160px;
    height: 160px;
    margin: 6px 0px;
  }
  
  .home-page .noAccount{
    font-size: 16px;
    line-height: 27px;
  }

  .home-page .btn-registration{
    font-size: 16px;
    line-height: 27px;
  }
  
  .home-page .logIn{
    font-size: 19px;
    line-height: 27px;
  }
}