.UserPage-h1 {
  padding: 0;
  margin: 0;
}

.UserPage .gohome {
  cursor: pointer;
  font-family: "Poppins";
  font-style: normal;
  color: #58929c;
  display: flex;
  justify-content: right;
  margin-right: 34px;
  margin-bottom: 73px;
}

.UserPage {
  background-image: url("../../img/10.jpg");
  background-size: 100%;
  height: 97vh;
}

.checksome {
  display: flex;
  flex-direction: column;
  margin-bottom: 30px;
}

.UserPage .aboveTheContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.UserPage .container {
  height: 486px;
  overflow-y: auto;
}

.UserPage .container1 {
  border: 4px solid white;
  background-color: rgba(255, 255, 255, 0.881);
  width: 298px;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5%;
}

.UserPage .Therapist {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  align-items: center;
  text-align: center;
  color: #005671;
}

.UserPage-h1 {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 600;
  font-size: 26px;
  text-align: center;
  color: #005671;
}

.text {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
  color: #000000;
}

.checksome .btn {
  border: none;
  opacity: 67%;
  color: white;
  font-size: 14px;
  font-weight: bold;
  transition: all 0.5s;
  cursor: pointer;
  background: #7ec8df;
  width: 241px;
}

.checksome .btnContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 5px;
}

@media only screen and (max-width: 1250px) {
  .UserPage {
    background-image: url("../../img/12.png");
  }
}

@media only screen and (max-width: 680px) {
  .UserPage {
    background-image: url("../../img/11.jpg");
  }
}

@media only screen and (max-width: 340px) {
  .UserPage .container1 {
    width: 252px;
  }

  .UserPage-h1 {
    font-size: 23px;
  }

  .text {
    font-size: 14px;
  }
}
