.questionHelp .realTimeAnxietyQuestionnaire,
.questionHelp .realTimeAnxietyQuestionnaire1 {
  padding: 0;
  margin: 0;
}

.questionHelp {
  background-image: url("../../img/10.jpg");
  background-size: 100%;
  height: 97vh;
}

.questionHelp .aboveTheContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.questionHelp .container {
  border: 4px solid white;
  background-color: rgba(255, 255, 255, 0.881);
  width: 425px;
  height: 488px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5%;
}

.questionHelp .realTimeAnxietyQuestionnaire {
  border-collapse: collapse;
  direction: rtl;
}

.questionHelp .realTimeAnxietyQuestionnaire1 {
  border-collapse: collapse;
  direction: ltr;
}

.questionHelp .msgContainer {
  display: flex;
  flex-direction: column;
  direction: rtl;
  align-self: flex-end;
}

.questionHelp .msgContainer1 {
  display: flex;
  flex-direction: column;
  direction: ltr;
  align-self: flex-start;
}

.questionHelp .h1Questionnaire {
  font-family: "Poppins";
  font-style: normal;
  font-size: 26px;
  line-height: 39px;
  color: #005671;
}

.questionHelp .h1Questionnaire1 {
  font-family: "Poppins";
  font-style: normal;
  font-size: 26px;
  line-height: 39px;
  color: #005671;
}

.questionHelp .realTimeAnxietyQuestionnaire .option,
.question {
  padding: 3px;
  text-align: center;
  border-bottom: 1px solid #ddd;
}

.questionHelp .realTimeAnxietyQuestionnaire .option {
  background-color: #add8e6;
  color: #fff;
}

.questionHelp .realTimeAnxietyQuestionnaire tr:nth-child(even) {
  background-color: #f2f2f2;
}

.questionHelp .realTimeAnxietyQuestionnaire1 .option,
.question {
  padding: 3px;
  text-align: center;
  border-bottom: 1px solid #ddd;
}

.questionHelp .realTimeAnxietyQuestionnaire1 .option {
  background-color: #add8e6;
  color: #fff;
}

.questionHelp .realTimeAnxietyQuestionnaire1 tr:nth-child(even) {
  background-color: #f2f2f2;
}

.questionHelp .btnOk {
  border: none;
  opacity: 67%;
  color: white;
  font-size: 24px;
  font-weight: bold;
  transition: all 0.5s;
  cursor: pointer;
  width: 218px;
  height: 41px;
  background: #7ec8df;
}

.questionHelp .BtnContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 15px;
}

.questionHelp .gohomeQuestionnaire {
  cursor: pointer;
  font-family: "Poppins";
  font-style: normal;
  color: #58929c;
  display: flex;
  justify-content: right;
  margin-right: 16px;
  margin-bottom: 103px;
  padding-right: 14px;
}

@media only screen and (max-width: 1250px) {
  .questionHelp {
    background-image: url("../../img/12.png");
  }
}

@media only screen and (max-width: 680px) {
  .questionHelp {
    background-image: url("../../img/11.jpg");
  }
}

@media only screen and (max-width: 500px) {
  .questionHelp .container {
    width: 351px;
    height: 455px;
  }

  .questionHelp .h1Questionnaire {
    font-size: 20px;
  }

  .questionHelp .realTimeAnxietyQuestionnaire1 .option,
  .question {
    font-size: 14px;
  }

  .questionHelp .realTimeAnxietyQuestionnaire1 .option {
    font-size: 14px;
  }

  .questionHelp .realTimeAnxietyQuestionnaire .option,
  .question {
    font-size: 14px;
  }

  .questionHelp .realTimeAnxietyQuestionnaire .option {
    font-size: 14px;
  }

  .questionHelp .btnOk {
    width: 173px;
    height: 32px;
    font-size: 20px;
  }

  .questionHelp .BtnContainer {
    padding: 10px;
  }
}

@media only screen and (max-width: 380px) {
  .questionHelp .container {
    width: 291px;
    height: 428px;
  }

  .questionHelp .h1Questionnaire {
    font-size: 17px;
  }

  .questionHelp .h1Questionnaire1 {
    font-size: 17px;
  }

  .questionHelp .msgContainer {
    font-size: 12px;
  }

  .question .msgContainer1 {
    font-size: 12px;
  }

  .questionHelp .realTimeAnxietyQuestionnaire1 .option,
  .question {
    font-size: 12px;
  }

  .questionHelp .realTimeAnxietyQuestionnaire1 .option {
    font-size: 12px;
  }

  .questionHelp .realTimeAnxietyQuestionnaire .option,
  .question {
    font-size: 12px;
  }

  .questionHelp .realTimeAnxietyQuestionnaire .option {
    font-size: 12px;
  }

  .questionHelp .btnOk {
    width: 150px;
    height: 27px;
    font-size: 15px;
  }

  .questionHelp .BtnContainer {
    padding: 5px;
  }
}
