.upload-more-diplomas-page h1 {
  padding: 0;
  margin: 0;
}

.upload-more-diplomas-page {
  background-image: url("../../img/10.jpg");
  background-size: 100%;
  height: 97vh;
}

.upload-more-diplomas-page .aboveTheContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.upload-more-diplomas-page .container {
  height: 486px;
  overflow-y: auto;
}

.upload-more-diplomas-page .container1 {
  border: 4px solid white;
  background-color: rgba(255, 255, 255, 0.881);
  width: 377px;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5%;
  gap: 15px;
}

.upload-more-diplomas-page h1 {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 600;
  font-size: 26px;
  text-align: center;
  color: #005671;
}

img {
  width: 198px;
}

.upload-more-diplomas-page .diploma {
  box-sizing: border-box;
  width: 333px;
  height: 42px;
  background: rgba(160, 164, 165, 0.29);
  border: 1px solid #000000;
}

.btnDiploma::-webkit-file-upload-button {
  visibility: hidden;
}

.btnDiploma::before {
  font-family: "Poppins";
  content: "Select diploma";
  display: inline-block;
  padding: 10px 2px;
  cursor: pointer;
  font-style: normal;
  font-size: 23px;
  font-weight: 300;
}

.btnDiploma:active::before {
  background: -webkit-linear-gradient(top, #e3e3e3, #f9f9f9);
}

.upload-more-diplomas-page .uploadFile {
  border: none;
  opacity: 67%;
  color: white;
  font-size: 18px;
  font-weight: bold;
  transition: all 0.5s;
  cursor: pointer;
  background: #7ec8df;
  height: 32px;
  width: 152px;
}

.upload-more-diplomas-page .gohome {
  cursor: pointer;
  font-family: "Poppins";
  font-style: normal;
  color: #58929c;
  display: flex;
  justify-content: right;
  margin-right: 16px;
  margin-bottom: 103px;
}

@media only screen and (max-width: 1250px) {
  .upload-more-diplomas-page {
    background-image: url("../../img/12.png");
  }
}

@media only screen and (max-width: 680px) {
  .upload-more-diplomas-page {
    background-image: url("../../img/11.jpg");
  }
}

@media only screen and (max-width: 410px) {
  .upload-more-diplomas-page .container {
    height: 415px;
  }

  .upload-more-diplomas-page .container1 {
    width: 247px;
  }

  .upload-more-diplomas-page h1 {
    font-size: 22px;
  }

  img {
    width: 144px;
  }

  .upload-more-diplomas-page .diploma {
    width: 238px;
    height: 34px;
  }

  .btnDiploma::before {
    margin-right: -65px;
    font-size: 19px;
  }

  .upload-more-diplomas-page .uploadFile {
    font-size: 18px;
  }
}
