.admin-home-page h1 {
  padding: 0;
  margin: 0;
}

.admin-home-page {
  background-image: url("../../img/10.jpg");
  background-size: 100%;
  height: 97vh;
}

.admin-home-page .aboveTheContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.admin-home-page .container {
  border: 4px solid white;
  background-color: rgba(255, 255, 255, 0.881);
  width: 286px;
  height: 393px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5%;
  gap: 22px;
}

.admin-home-page h1 {
  width: 252px;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 600;
  font-size: 24px;
  line-height: 36px;
  color: rgba(147, 110, 82, 0.88);
  padding-bottom: 26px;
}

.admin-home-page .btn-FaUserCheck {
  border: none;
  border-radius: 50%;
  box-sizing: border-box;
  width: 100px;
  height: 100px;
  background: rgba(44, 197, 244, 0.52);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.admin-home-page .FaUserCheck {
  width: 63px;
  height: 48px;
}

.admin-home-page .btn-FaUserPlus {
  border: none;
  border-radius: 50%;
  box-sizing: border-box;
  width: 100px;
  height: 100px;
  background: rgba(44, 197, 244, 0.52);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.admin-home-page .FaUserPlus {
  width: 60px;
  height: 68px;
}

.admin-home-page .btn-FaUserCog {
  border: none;
  border-radius: 50%;
  box-sizing: border-box;
  width: 100px;
  height: 100px;
  background: rgba(44, 197, 244, 0.52);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.admin-home-page .FaUserCog {
  width: 60px;
  height: 68px;
}

.admin-home-page .btn-FaUserTimes {
  border: none;
  border-radius: 50%;
  box-sizing: border-box;
  width: 100px;
  height: 100px;
  background: rgba(44, 197, 244, 0.52);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.admin-home-page .FaUserTimes {
  width: 63px;
  height: 48px;
}

.admin-home-page .logout {
  cursor: pointer;
  font-family: "Poppins";
  font-style: normal;
  color: #58929c;
  display: flex;
  justify-content: right;
  margin-right: 16px;
  margin-bottom: 155px;
}

@media only screen and (max-width: 1250px) {
  .admin-home-page {
    background-image: url("../../img/12.png");
  }
}

@media only screen and (max-width: 680px) {
  .admin-home-page {
    background-image: url("../../img/11.jpg");
  }
}

@media only screen and (max-width: 320px) {
  .admin-home-page .container {
    width: 257px;
    height: 288px;
  }

  .admin-home-page h1 {
    font-size: 20px;
  }

  .admin-home-page .btn-FaUserCheck {
    width: 80px;
    height: 80px;
  }

  .admin-home-page .FaUserCheck {
    width: 53px;
    height: 38px;
  }

  .admin-home-page .btn-FaUserPlus {
    width: 80px;
    height: 80px;
  }

  .admin-home-page .FaUserPlus {
    width: 50px;
    height: 58px;
  }

  .admin-home-page .btn-FaUserCog {
    width: 80px;
    height: 80px;
  }

  .admin-home-page .FaUserCog {
    width: 50px;
    height: 58px;
  }

  .admin-home-page .btn-FaUserTimes {
    width: 80px;
    height: 80px;
  }

  .admin-home-page .FaUserTimes {
    width: 53px;
    height: 38px;
  }
}
