.addAmin {
  background-image: url("../../img/10.jpg");
  background-size: 100%;
  height: 97vh;
}

.addAmin .aboveTheContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.addAmin .container {
  border: 4px solid white;
  background-color: rgba(255, 255, 255, 0.881);
  width: 392px;
  height: 437px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5%;
  gap: 3px;
}

.addAmin form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
}

.addAmin h1 {
  color: #005671;
  text-align: center;
  font-family: "Poppins";
  font-style: normal;
  font-size: 26px;
}

.addAmin .text {
  font-family: "Poppins";
  font-weight: 300;
  font-size: 20px;
}

.addAmin .textbox {
  box-sizing: border-box;
  width: 153px;
  height: 42px;
  background: rgba(160, 164, 165, 0.29);
  border: 1px solid #000000;
}

.addAmin .emailTextBox {
  box-sizing: border-box;
  width: 327px;
  height: 42px;
  background: rgba(160, 164, 165, 0.29);
  border: 1px solid #000000;
}

.addAmin .textbox::placeholder,
.addAmin .emailTextBox::placeholder {
  color: black;
}

.addAmin .btn {
  border: none;
  opacity: 67%;
  color: white;
  font-size: 24px;
  font-weight: bold;
  transition: all 0.5s;
  cursor: pointer;
  width: 327px;
  height: 39px;
  background: #7ec8df;
}

.addAmin .gohome {
  cursor: pointer;
  font-family: "Poppins";
  font-style: normal;
  color: #58929c;
  display: flex;
  justify-content: right;
  margin-right: 16px;
  margin-bottom: 103px;
}

@media only screen and (max-width: 1250px) {
  .addAmin {
    background-image: url("../../img/12.png");
  }
}

@media only screen and (max-width: 680px) {
  .addAmin {
    background-image: url("../../img/11.jpg");
  }
}

@media only screen and (max-width: 450px) {
  .addAmin .container {
    width: 284px;
    height: 363px;
  }

  .addAmin h1 {
    font-size: 23px;
  }

  .addAmin .text {
    font-size: 16px;
  }

  .addAmin .textbox {
    width: 129px;
    height: 35px;
  }

  .addAmin .emailTextBox {
    width: 279px;
    height: 35px;
  }

  .addAmin .btn {
    width: 279px;
    height: 35px;
    font-size: 20px;
  }
}
