.therapis-home-page h1 {
  padding: 0;
  margin: 0;
}

.therapis-home-page {
  background-image: url("../../img/10.jpg");
  background-size: 100%;
  height: 97vh;
}

.therapis-home-page .aboveTheContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.therapis-home-page .container {
  border: 4px solid white;
  background-color: rgba(255, 255, 255, 0.881);
  width: 294px;
  height: 366px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5%;
  gap: 10px;
}

.therapis-home-page h1 {
  width: 252px;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 600;
  font-size: 24px;
  line-height: 36px;
  color: rgba(147, 110, 82, 0.88);
}

.therapis-home-page .par-wrapper {
  width: 246px;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 600;
  font-size: 24px;
  line-height: 36px;
  text-align: center;
  color: rgba(162, 121, 90, 0.88);
}

.therapis-home-page .btn-FaUserCog {
  border: none;
  border-radius: 50%;
  box-sizing: border-box;
  width: 100px;
  height: 100px;
  background: rgba(44, 197, 244, 0.52);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.therapis-home-page .FaUserCog {
  width: 63px;
  height: 48px;
}

.therapis-home-page .btn-MdUploadFile {
  border: none;
  border-radius: 50%;
  box-sizing: border-box;
  width: 100px;
  height: 100px;
  background: rgba(44, 197, 244, 0.52);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.therapis-home-page .MdUploadFile {
  width: 63px;
  height: 48px;
}

.therapis-home-page .logout {
  cursor: pointer;
  font-family: "Poppins";
  font-style: normal;
  color: #58929c;
  display: flex;
  justify-content: right;
  margin-right: 16px;
  margin-bottom: 161px;
}

@media only screen and (max-width: 1250px) {
  .therapis-home-page {
    background-image: url("../../img/12.png");
  }
}

@media only screen and (max-width: 680px) {
  .therapis-home-page {
    background-image: url("../../img/11.jpg");
  }
}

@media only screen and (max-width: 350px) {
  .therapis-home-page .container {
    width: 260px;
    height: 283px;
  }

  .therapis-home-page h1 {
    font-size: 20px;
  }

  .therapis-home-page .par-wrapper {
    font-size: 20px;
  }

  .therapis-home-page .btn-FaUserCog {
    width: 75px;
    height: 75px;
  }

  .therapis-home-page .FaUserCog {
    width: 50px;
    height: 35px;
  }

  .therapis-home-page .btn-MdUploadFile {
    width: 75px;
    height: 75px;
  }

  .therapis-home-page .MdUploadFile {
    width: 50px;
    height: 45px;
  }
}
